.calculator{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
    width: 300px;
    height: 400px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
    padding: 10px;
    
}

button{
    cursor: pointer;
}

.display{
    grid-column: 1 / 5;
    grid-row: 1 / 2;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
    padding: 10px;
    text-align: right;
    font-size: 2rem;
    font-weight: 700;
    color: #000;
    word-wrap: break-word;
}

.del{
    background-color: cyan;
}

.reset{
    grid-column: 1 / 3;
    /* grid-row: 2 / 3; */
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    text-align: center;
    font-size: 1.1rem;
    font-weight: 700;
    color: #000;
}

.equal{
    grid-column: 3/ 5;
    /* grid-row: 1 / 3; */
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    text-align: center;
    font-size: 1.1rem;
    font-weight: 700;
    color: #000;
}


.operator{
    background-color: orange;
}